
















import Vue from "vue";
import Component from "vue-class-component";
import { PostModel } from "@/service/interfaces";
import { AppService } from "../../service/app.service";
import PostViewComponent from "../../components/PostView.vue";

@Component({
  components: {
    PostViewComponent,
  },
}) // without this, Vue lifecycle methods will not be invoked
export default class PostView extends Vue {
  loading = false;
  post: PostModel = new PostModel();
  app = AppService.instance;
  async mounted(): Promise<void> {
    this.loading = true;
    try {
      const res = await this.app.api.postGet({ path: location.href });
      // console.log("post", res);
      this.loading = false;
      this.post = this.post.fromMap(res);
    } catch (e) {
      this.loading = false;
      this.app.error(e);
    }
  }

  onPostDeleted(idx: string): void {
    // TODO: move back to list
    console.log(`Post ${idx} deleted. todo: move back to list.`);
  }
}
